import moment from 'moment'
import { GET_STOCK_TAKE_ACTION, CREATE_JOB_SHEET, SET_AREA, IS_LOADING } from 'apiServices/action'
import { getPoBySkuId, removeEntryModule } from 'apiServices'
import { getGrade } from 'apiServices/dropdown'
import { isFloat,dateDMY } from 'services'

// update sku list tobe selected
export const selectSku = async ({
    idx,
    checked,
    dispatch,
    setLocalEntryData,
    moduleName,
    moduleSummary,
    moduleEntry,
    warehouseUuid,
    areaId,
    zeroQuantity }) => {
    let moduleSummaryNew = [...moduleSummary]
    moduleSummaryNew[idx].checked = checked
    dispatch({ type: GET_STOCK_TAKE_ACTION, [`${moduleName}summaryData`]: moduleSummaryNew, finish:false })

    const selectedData = moduleSummaryNew?.filter(ldata => {
        if (ldata.checked) return ldata
    })
    let jsSelector = []
    if (moduleEntry) {
        moduleEntry.jobPost.list.map(data => {
            const jsList = (({ skuUuid }) => ({ skuUuid }))(data)
            if (jsList.skuUuid) {
                jsSelector.push(jsList.skuUuid)
            }
        })
    }
    let jsD = []
    const a = selectedData.map(async (list, sI) => {
        const isInclude = jsSelector.includes(list.skuUuid)
        if (!isInclude) {
            const grade = await getGrade({ skuId: list.skuUuid })
            list['gradeList'] = grade
            const data = await getPoBySkuId({ skuId: list.skuUuid, warehouseUuid, dispatch, areaId: areaId.value, moduleName,zeroQuantity })
            if (data) {
                const grades = list.gradeList.filter(grade => { return grade?.value === list.skuUuid })
                const addPoItem = data.map(poList => {
                    poList.itemList = [
                        {
                        qty: poList.initialWeight,
                        grade: grades[0],
                        // ripeness: { value: String(poList.ripenessLevel), label: poList.ripenessLevel },
                        expiryDate:poList?.expiryDate,
                        expiryDateText:dateDMY({date:poList?.expiryDate})
                        }
                    ]
                    poList.initialWeightUpdate = poList.inboundQty - poList.initialWeight
                    poList.initialWeightScreenUpdate = parseInt(0)
                    const expiryMs = poList.expiryDate ? moment(poList.expiryDate).valueOf() : null
                    const inBoundyMs = poList.inboundDate ? moment(poList.inboundDate).valueOf() : null
                    poList.expiryDate = expiryMs
                    poList.inboundDate = inBoundyMs
                    poList.skuUuid = list.skuUuid
                    poList.uom = list.unitOfMeassures
                    poList.category = list.category
                    poList.initialWeight = Number.isInteger(poList.initialWeight) ? parseInt(poList.initialWeight) : parseFloat(poList.initialWeight)
                    return poList
                })
                list['poList'] = addPoItem
                jsD.push(list)
            }
        }
    })
    const aF = await Promise.all(a)
    dispatch({type:IS_LOADING, finish:true})
    let newJs = []
    if (moduleEntry) {
        newJs = moduleEntry.jobPost.list.concat(jsD)
    }
    if (!moduleEntry) newJs = jsD

    const uncheckedSku = moduleSummaryNew[idx]
    if (!uncheckedSku.checked) {
        newJs = newJs.filter(data => { return data.skuUuid !== uncheckedSku.skuUuid })
    }

    const id = moduleEntry?.jobPost?.id
    const jobSheet = {
        jobPost: {
            id: id ? id : 0,
            warehouseSource: warehouseUuid,
            warehouseDestination: warehouseUuid,
            areaSource: parseInt(areaId?.value),
            areaName: areaId?.label,
            areaDestination: parseInt(areaId?.value),
            list: newJs
        }
    }
    if (newJs.length === 0) dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: null })
    setLocalEntryData(jobSheet)
}

//pre Add SKU
export const preAddSku = async ({
    zeroQuantity,
    idx,
    checked,
    dispatch,
    moduleName,
    moduleSummary,
    moduleEntry,
    warehouseUuid,
    areaId,
    preAddHandler }) => {
    let moduleSummaryNew = [...moduleSummary]
    moduleSummaryNew[idx].checked = checked

    const selectedData = moduleSummaryNew?.filter(ldata => {
        if (ldata.checked) return ldata
    })
    let jsSelector = []
    if (moduleEntry) {
        moduleEntry.map(data => {
            const jsList = (({ skuUuid }) => ({ skuUuid }))(data)
            if (jsList.skuUuid) {
                jsSelector.push(jsList.skuUuid)
            }
        })
    }
    let jsD = []
    const a = selectedData.map(async (list, sI) => {
        const isInclude = jsSelector.includes(list.skuUuid)
        if (!isInclude) {
            const grade = await getGrade({ skuId: list.skuUuid })
            list['gradeList'] = grade
            const data = await getPoBySkuId({ skuId: list.skuUuid, warehouseUuid, dispatch, areaId: areaId.value, moduleName,zeroQuantity })
            if (data) {
                const grades = list.gradeList.filter(grade => { return grade?.value === list.skuUuid })
                const addPoItem = data.map(poList => {
                    poList.itemList = [
                        {
                            qty: poList.initialWeight,
                            grade: grades[0],
                            // ripeness: { value: String(poList.ripenessLevel), label: poList.ripenessLevel },
                            expiryDate:poList?.expiryDate,
                            expiryDateText:dateDMY({date:poList?.expiryDate})
                        }
                    ]
                    poList.initialWeightScreenUpdate = parseInt(0)
                    poList.initialWeightUpdate = poList.inboundQty - poList.initialWeight
                    const expiryMs = poList.expiryDate ? moment(poList.expiryDate).valueOf() : null
                    const inBoundyMs = poList.inboundDate ? moment(poList.inboundDate).valueOf() : null
                    poList.expiryDate = expiryMs
                    poList.inboundDate = inBoundyMs
                    poList.skuUuid = list.skuUuid
                    poList.uom = list.unitOfMeassures
                    poList.category = list.category
                    poList.initialWeight = isFloat(poList.initialWeight) ? isFloat(poList.initialWeight) : poList.initialWeight
                    return poList
                })
                list['poList'] = addPoItem
                jsD.push(list)
            }

        }
    })
    const aF = await Promise.all(a)
    let newJs = []
    if (moduleEntry) {
        newJs = moduleEntry.concat(jsD)
    }
    if (!moduleEntry) newJs = jsD

    const uncheckedSku = moduleSummaryNew[idx]
    if (!uncheckedSku.checked) {
        newJs = newJs.filter(data => { return data.skuUuid !== uncheckedSku.skuUuid })
    }
    preAddHandler(newJs);
}

export const submitValidation = ({ moduleEntry, moduleName, setConfirmation, dispatch, setIsRequired }) => {
    let isClear = []
    let js = { ...moduleEntry }

    const jsR = moduleEntry.jobPost.list.map(data => {
        data.poList.map(po => {
            po.itemList.map(item => {
                const { qty, grade, ripeness, note } = item

                if (po.category === 'Fruits') {
                    if ((qty == 0 || isNaN(qty)) && grade?.value === null) {
                        item.qtyIsEmpty = false
                        item.gradeIsEmpty = false
                        item.ripenessIsEmpty = false
                        item.noteIsEmpty = false
                    }
                    else {
                        if (item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste') {
                            isNaN(qty) || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                            !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                            grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                        }
                        else {
                            isNaN(qty) || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                            !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                            !ripeness?.value ? item.ripenessIsEmpty = true : item.ripenessIsEmpty = false
                            grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                        }
                    }
                }
                else {

                    if ((qty == 0 || isNaN(qty)) && grade?.value === null) {
                        item.qtyIsEmpty = false
                        item.gradeIsEmpty = false
                        item.ripenessIsEmpty = false
                        item.noteIsEmpty = false
                    }
                    else {
                        isNaN(qty) || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                        !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                        grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                    }
                }

                if (item?.qtyIsEmpty || item?.gradeIsEmpty || item?.ripenessIsEmpty || item?.noteIsEmpty || item?.qtyToCompareIsEmpty) isClear.push(false)
                else isClear.push(true)
                return item
            })
            return po
        })
        return data
    })


    //validate there is value
    let valid = false
    jsR.map(data => {
        data.poList.map(po => {
            po.itemList.map(item => {
                let i = false
                if (item?.qty >= 0) valid = true
            })
        })
    })

    js.jobPost.list = jsR
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
    if (!isClear.includes(false) && valid) {
        setConfirmation({ show: true, name: 'submit' })
    }
    else {
        setConfirmation({ show: false, name: null })
        setIsRequired(true)
    }
}